	back(e){

		if ((this.state.noinet == 0)&&(this.state.popout===""||this.state.popout==null)) {
			if (this.state.activeView === "my" || this.state.activeView === "adminka") {

				if (e.state.panel === "home") {
					this.setState({activePanel: e.state.panel})
					this.setState({activeView: "glav"})
				}

			} else {
				if (e.state) {
					if (e.state.panel === "my" || this.state.activeView === "adminka") {
					} else {
						this.setState({activePanel: e.state.panel})
						this.setState({activeView: "glav"})
						console.log(e.state.panel)


					}
				} else {
					this.setState({activePanel: "home"})
					window.history.pushState({panel: "home"}, 'home')


				}
			}
		} else {
			window.history.pushState({panel: "home"}, 'home')
		}



	}



	async componentWillMount() {

		window.addEventListener('offline', this.noinet.bind(this));
		window.addEventListener('online', this.yesinet.bind(this));
		window.addEventListener('popstate',e=>e.preventDefault() & this.back(e))
		window.history.pushState({panel:'home'},'home')
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppAccessTokenFailed':
					this.setState({popout:""})
					this.openDefault("Ошибка","Невозможно авторизоваться через приложение. Попробуйте m.vk.com")
					this.setState({scheme:document.body.getAttribute("scheme")})
					let schemeAttribute = document.createAttribute('scheme');
					schemeAttribute.value = 'client_light';
					document.body.attributes.setNamedItem(schemeAttribute);
					connect.send("VKWebAppSetViewSettings", {"status_bar_style": "dark", "action_bar_color": "#EEE393"});
					this.setState({activeView:"hello"})

					break;
				case 'VKWebAppSetLocationResult':

					break;
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });

					setTimeout(()=>{
						var t = this.get("https://api.finebot.site/events/account.check?uid="+this.state.fetchedUser.id)
						if(t==="yes"){
							this.setState({lm:1})

							connect.send("VKWebAppGetAuthToken", {"app_id": 7077031, "scope": "friends"});

						}else{

							if(e.detail.data.city!==undefined) {


								this.setState({lm: e.detail.data.city.title})
							}
							connect.send("VKWebAppSetViewSettings", {"status_bar_style": "dark", "action_bar_color": "#EEE393"});

							this.setState({activeView:"hello",popout:null})
							this.setState({scheme:document.body.getAttribute("scheme")})

							let schemeAttribute = document.createAttribute('scheme');
							schemeAttribute.value = 'client_light';
							document.body.attributes.setNamedItem(schemeAttribute);
						}
					})

					break;
				case 'VKWebAppAccessTokenReceived':
					var token= e.detail.data.access_token;
					this.setState({token:token,activeView:"glav"})
					setTimeout(this.setState({popout:<ScreenSpinner />}))

					if(!(this.state.scheme==0)){
						let schemeAttribute = document.createAttribute('scheme');
						schemeAttribute.value = this.state.scheme;
						document.body.attributes.setNamedItem(schemeAttribute);
					}


					if(!(this.state.lm==1)){
						this.get("https://api.finebot.site/events/account.editCity?token="+this.state.token+"&city="+encodeURIComponent(this.state.lm))
						this.setState({lm:1})
					}

					setTimeout(this.load.bind(this))

					// ТУТ ЛОАД




					var hash = window.location.hash;
					if(hash.startsWith("#e")){
						setTimeout(this.startloading.bind(this))
						setTimeout(this.setState({eid:hash.split("#e")[1],activePanel:"full"}))
					}
					break;
				case 'VKWebAppAllowMessagesFromGroupResult':
					this.setState({allownotify:"1"})
					this.geta("https://api.finebot.site/events/account.notify?token="+this.state.token)



					break;
				case 'VKWebAppAllowMessagesFromGroupFailed':
					this.setState({allownotify:"0",notify:""})
					this.setState({notify:<Cell className="noselect" onChange={this.Sub.bind(this)}asideContent={<Switch id="f5"/>}>Подписка на уведомления</Cell>})
					this.geta("https://api.finebot.site/events/account.notify?token="+this.state.token)


					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
		connect.send("VKWebAppSetLocation", {"location": "home"});
	}

