	updateTheme(){
		if(document.body.attributes.getNamedItem("scheme")==null){
			return
		}

		switch (this.state.theme) {
			case 0:
				document.body.attributes.getNamedItem("scheme").value=document.body.attributes.getNamedItem("default_theme").value
				switch (document.body.attributes.getNamedItem("default_theme").value) {
					case 'client_light':
						connect.send("VKWebAppSetViewSettings", {"status_bar_style": "light", "action_bar_color": "#5281B9"});
						break;
					case 'client_dark':
						connect.send("VKWebAppSetViewSettings", {"status_bar_style": "light", "action_bar_color": "#2C2D2F"});
						break;
					case 'bright_light':
						connect.send("VKWebAppSetViewSettings", {"status_bar_style": "dark", "action_bar_color": "#fff"});
						break;
					case 'space_gray':
						connect.send("VKWebAppSetViewSettings", {"status_bar_style": "light", "action_bar_color": "#191919"});
						break;
				}
				break;
			case 1:
				document.body.attributes.getNamedItem("scheme").value="client_light"
				connect.send("VKWebAppSetViewSettings", {"status_bar_style": "light", "action_bar_color": "#5281B9"});

				break;
			case 2:
				document.body.attributes.getNamedItem("scheme").value="client_dark"
				connect.send("VKWebAppSetViewSettings", {"status_bar_style": "light", "action_bar_color": "#2C2D2F"});

				break;
			case 3:
				document.body.attributes.getNamedItem("scheme").value="bright_light"
				connect.send("VKWebAppSetViewSettings", {"status_bar_style": "dark", "action_bar_color": "#fff"});

				break;
			case 4:
				document.body.attributes.getNamedItem("scheme").value="space_gray"
				connect.send("VKWebAppSetViewSettings", {"status_bar_style": "light", "action_bar_color": "#191919"});

				break;
		}
	}
